.header {
    padding: 20px 0;
    cursor: default;
    user-select: none;
    //box-shadow: @boxShadow;
    //margin-bottom: 2px;
    background: @bgColorNormal;
    border-bottom: 1px solid #e0e0e0;
    line-height: 20px;

    @media @bpMedium {
        padding: 25px 0 20px;
    }

    @media @bpSmallOnly {
        padding: 20px 0 40px;
    }

    @media @bpExtraSmall {
        text-align: center;
        line-height: 1;
    }

    &_inner-help {
        @media @bpLessMedium {
            text-align: center;
            line-height: 1;
            position: fixed;
            left: 0;
            right: 0;
            top: 0;
            z-index: 5;
            padding: 20px 0;
        }

        @media @hideHeader {
            display: none;
        }
    }

    &__title {
        font-size: 0.875rem;
        font-weight: 600;
        color: @textColorAccented;

        .ie7 &, .ie8 & {
            font-size: 14px;
        }

        &_product {
            direction: ltr;
            position: relative;
            white-space: nowrap;
            overflow: hidden;
            padding: 0 30px;

            &:after {
                content: '';
                background:linear-gradient(to right, rgba(255, 255, 255, 0) 0%, #FFFFFF 50%);
                height: 100%;
                position: absolute;
                right: 0;
                top: 0;
                width: 50px;
            }

            @media @bpMedium {
                display: none;
            }
        }
    }

    &__title-big {
        font-size: 1.25rem;
        font-weight: 700;
        color: @textColorLink;
        text-decoration: underline;

        @media @bpExtraSmall {
            font-size: 0.875rem;
            font-weight: 600;
            display: inline;
        }

        .ie7 &, .ie8 & {
            font-size: 20px;
        }
    }

    &__title-link {
        color: @textColorLink;
        text-decoration: none;
    }

    &__logo {
        .text-align(right);
        padding: @space*.5 0;
    }

    &__mobile-menu-btn {
       .icon-single(16px, 16px, "icons/contents-mobile");
        border: 0;
        padding: 0;
        position: absolute;
        z-index: 1;
        .left(20px);
        top: 18px;

        &.is-active {
            .icon-single(16px, 16px, "icons/go-back-green");
            transform: scale(-1, 1);
        }

        @media @bpMedium {
            &, &.is-active {
                display: none;
            }
        }
    }

    &__search {
        position: absolute;
        top: 8px;
        left: 40px;
        right: 20px;
        .rtl(left, 20px);
        .rtl(right, 40px);

        @media @bpMedium {
            display: none;
        }
    }

    &__search-btn {
        .icon-single(16px, 16px, "icons/search-green");
        border: 0;
        padding: 0;
        position: absolute;
        top: 10px;
        .right(0);

        transition: @transition;
        transition-property: right;

        &.is-active {
            .icon-single(16px, 16px, "icons/search-gray");
            right: auto;
            .left(10px);
            .margin(0, -16px, 0, 0);
        }
    }

    &__clear-btn {
        .icon-single(16px, 16px, "icons/clear");
        border: 0;
        padding: 0;
        position: absolute;
        top: 10px;
        .right(0);

        &.is-hidden {
            visibility: hidden;
        }
    }

    &__search-input {

        input& {
            border: 0;
            font-family: @fontFamily;
            font-size: 1.125rem;
            color: @textColorAccented;
            padding: 8px 30px 8px 40px;
            width: 100%;

            transition: @transition;
            transition-property: opacity, width, visibility;

            &.is-hidden {
                opacity: 0;
                width: 32px;
                visibility: hidden;
            }

            .ie7 &, .ie8 & {
                font-size: 18px;
            }

            [lang="el-GR"] &,
            [lang="vi-VN"] &  {
                font-family: @fontFamilyGreece;
            }
        }

        &::placeholder {
            color: fade(@textColorAccented, 40);
            font-weight: 100;
            font-style: italic;

            .rtl(font-style, normal);
        }
    }
}