@import (reference) "../styles";

.contents {
    @levelOffset: @grid*3.5;
    @itemVPadding: 7px;
    @iconOffset: 2px;

    list-style: none;
    cursor: default;
    margin: 20px 0 0;
    .padding(0, 0, 0, 0);
    font-size: 0.9rem;
    zoom: 1;
    position: relative;

    @media @bpLessMedium {
        font-size: 0.9rem;
        margin-top: 0;
    }

    &__item {
        position: relative;
        margin: 0;

        &-link {
            position: relative;
            display: block;
            color: @textColorNormal;
            text-decoration: none;
            vertical-align: middle;
            line-height: 1.2;
            .margin (0, @grid*4, 0, @grid*6.5);
            border-bottom: 1px solid fade(@textColorSecondary, 25);
            padding: 8px 0;
            word-wrap: break-word;

            &:active {
                //color: @textColorLink;
                //font-weight: 600;
            }

            &:focus {
                outline: none;
            }

            &:hover,
            &:visited {
                color: @textColorNormal;
            }

            .ie7 & {
                *display: inline; zoom: 1;
                .margin ( @grid*2, 0, @grid*2, 0 );
                .padding ( 0, 0, 0, @grid*5.5 );
            }

            .ie7 &, .ie8 & {
                border-bottom: 1px solid rgb(229, 229, 229);
            }

            @media @bpLessMedium {
                .margin (0, 0, 0, @grid*6.5);
                color: @textColorAccented;
                .padding(10px, 50px, 10px, 0);
            }
        }

        &--parent {
            text-decoration: underline;
        }
    }

    & & {
        .padding ( 0, 0, 0, @grid*5 );
        margin: 0;
        list-style: none;
    }

    &__toggle {
        .left(0);
        position: absolute;
        top: 8px;
        border: none;
        cursor: pointer;
        vertical-align: middle;
        user-select: none;
        padding: 0;

        @width: 12px;
        @height: 12px;

        .icon-single(@width, @height, "icons/expand");

        &.is-toggled {
            .icon-single(@width, @height, "icons/collapse");
        }
        
        @media @bpLessMedium {
            top: 14px;
        }
    }

    &__item.is-active > &__item-link {
        color: @textColorLink;
        font-weight: 600;
        font-size: 96%;
        padding: 7px 0 9px;
    }

    .ie7 &, .ie8 & {
        font-size: 14px;
    }
    
    &_all-in-one &__item {
        margin: 0.5em 0;
    }
}