@import (reference) "../styles";

.search {

    &__wrapper {
        position: relative;
        .margin(0, @grid*4, 0, 0);

        .ie7 & {
            .padding( 0, 0, 0, 0 );
        }
    }

    input&__field {
        width: 100%;
        line-height: 1.2;
        .padding(@grid*2, @grid*6, @grid*2 - 1, 0);
        margin: 1px 0;
        font-family: @fontFamily;
        font-size: 1.125rem;
        font-weight: 100;
        font-style: italic;
        color: #b1b1b1;
        border: 0;
        border-bottom: 1px solid fade(@textColorSecondary, 25);

        &::-ms-clear{
            display: none;
        }

        .ie7 & {
            .padding(@grid*2, 0, @grid*2 - 1, 0);
        }

        .ie8 & {
            .padding(@grid*2, 0, @grid*2 - 1, @grid*3);
        }

        .ie7 &, .ie8 & {
            font-size: 18px;
            border-bottom: 1px solid rgb(229, 229, 229);
        }

        [lang="el-GR"] &,
        [lang="vi-VN"] & {
            font-family: @fontFamilyGreece;
        }

        .rtl(font-style, normal);
    }

    &__btn {
        @width:  30px;
        @height: 34px;
        @x: -250px;
        @y: -200px;
        @size: 13px;

        border: none;
        background: none;

        position: absolute;
        top: 0;
        .right();
        bottom: 0;
        width: @width;
        height: @height;
        display: block;
        //pointer-events: none;

        &:after {
            content: '';
            .icon-single(16px, 16px, "icons/search-gray");
            margin: 9px 0 0;

            .ie7 &,
            .ie8 & {
                //content: none;
            }
        }

        .ie7 &,
        .ie8 & {
            .icon();
            background-position: @x+((@width - @size)/2) @y+((@height - @size)/2)-2;
            background-color: @bgColorNormal;
            top: 3px;
            .right( 1px );
            height: @height - 6;
            padding: 0;
        }

        &:hover:after {
            opacity: .8;
        }

        &.is-hidden {
            display: none;
        }
    }

    &__clear {
        .icon-single(16px, 16px, "icons/clear");
        border: 0;
        padding: 0;
        position: absolute;
        top: 12px;
        cursor: pointer;
        .right(8px);

        &.is-hidden {
            display: none;
        }
    }
}