@import (reference) "../styles";

.bottom-links {
    border-top: 1px solid fade(@textColorSecondary, 25);
    padding: 20px 0 0;
    margin: 40px 0 0;

    .ie7 &, .ie8 & {
        border-top: 1px solid rgb(229, 229, 229);
    }


    &__btn {
        display: none;
        vertical-align: top;
        width: 34px;

        &--prev {
            .text-align( left );

            .ie7 & {
                position: absolute;
                .left();
                top: 0;
            }
        }

        &--next {
            .text-align( right );

            .ie7 & {
                position: absolute;
                .right();
                top: 0;
            }
        }
    }

    &__btn-link {
        display: block;
        width: 34px;
        height: 34px;
    }

    &__link {

        .bt-home {
            display: none;
            .underline();

            &:before {
                display: inline-block;
                content: '';
                width: 7px;
                height: 10px;
                background-image: url(data:image/png;base64,);
                background-repeat: no-repeat;
                .margin(0, 10px, 0, 0);
            }

            &.is-shown {
                display: block;
            }
        }
    }

    &__service-info {
        color: @textColorSecondary;
        font-size: 0.85rem;
        margin: 0 0 20px;

        .ie7 &, .ie8 & {
            font-size: 14px;
        }
    }
}