@import (reference) "../styles";

.dropdown {
    @dropdownBgColor: @bgColorNormal;
    @dropdownItemPaddingDelta: @grid;
    position: relative;
    display: inline-block;
    max-width: 100%;
    //font-size: 0;

    .ie7 & {
        *display: inline; zoom: 1;
        z-index: 11;
    }

    &__btn {
        max-width: 100%;
        //overflow: hidden;
        //text-overflow: ellipsis;

        display: inline-block;
        position: relative;
        z-index: 2;
        .padding(7px, 20px, 6px, 10px);

        border: 1px solid #d8d8d8;
        background: @dropdownBgColor;
        color: @textColorAccented;

        font-size: 0.875rem;
        text-decoration: none;

        cursor: pointer;
        user-select: none;

        transition: background @transitionOnHover, color @transitionOnHover;

        &:active {
            opacity: 1;
            background: fade( @bgColorAccented, 50% );

            .ie7 &, .ie8 & {
                background: rgb(248, 250, 251);
            }
        }

        .ie7 & {
            min-width: 40px;
        }

        .ie7 &, .ie8 & {
            font-size: 14px;
        }
    }

    &__arrow {
        position: absolute;
        .right(8px);
        top: 50%;
        margin-top: -1px;
        
        //.margin(-1px, -@dropdownItemPaddingDelta, @grid/2, @grid);
        width: 6px;
        height: 3px;
        background-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAYAAAADCAYAAACwAX77AAAAAXNSR0IArs4c6QAAACtJREFUCB1jvHnzZsP////rGZAAIyNjI5gLkrxx48Z/EAaxkdQwMIAEkAUBIi8aR72nisMAAAAASUVORK5CYII=);
        background-repeat: no-repeat;

        @media @hidpi {
            background-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAwAAAAGCAYAAAD37n+BAAAAAXNSR0IArs4c6QAAAEZJREFUGBmNzoEJACAIRNFuGsesttRpjAuKwCKFQOJ9EGbW3L2WxADooMtExCLSZvCLFqbbwSs6MU0Ynqeqzsc9gNsH4QsP9roySgNwjsEAAAAASUVORK5CYII=);
            background-size: 6px;
        }

        .ie7 & {
            //.margin(1px, -@dropdownItemPaddingDelta, @grid/2, @grid);
        }
    }

    &__list {
        .left();
        position: absolute !important;
        display: block;
        top: 100%;
        min-width: 100%;

        border: 1px solid #d8d8d8;
        background: @dropdownBgColor;
        font-size: 0.875rem;
        margin: -1px 0 0;
        .padding(0, 0, 0, 0);

        visibility: hidden;
        list-style: none;
        overflow: auto;

        white-space: nowrap;

        .text-align( left );

        .ie7 & {
            visibility: visible;
            display: none;
            margin: -1px;
            width: 200%;
        }

        .ie7 &, .ie8 & {
            font-size: 14px;
        }

        &_opened {
            visibility: visible;
            z-index: 1999;
            animation: dropdown .2s 1;

            .ie7 & {
                display: block;
                visibility: visible;
                overflow: scroll;
            }
        }

        .scrollbar( @grid*2, 0 );

        @-moz-document url-prefix() {
            &_firefox-fix {
                .dropdown__link {
                    .padding(7px, 34px, 6px, 10px);
                }
            }
        }
    }
    
    &__item {
        margin: 0;

        .ie7 & {
            border: none;
            padding: 0;
            margin: 0;
        }
    }

    &_right &__list {
        @media @bpMedium {
            .right()
        }
    }

    &__link {
        display: block;
        .padding(7px, 14px, 6px, 10px);
        text-decoration: none;
        color: inherit;

        .ie7 & {
            color: @textColorAccented;
            margin: 0;
        }

        &:hover {
            color: @textColorAccented;
            background: @bgColorAccented;
        }

        &:visited {
            color: inherit;
        }
    }

    &_large {
        width: 100%;
    }

    &_large &__btn {
        width: 100%;
        font-size: 0.875rem;
        font-weight: 300;
        .text-align(left);
        padding: 10px;
        box-shadow: 0 1px 0 0 rgba(0,0,0,0.12);

        &:after {
            float: right;
            margin: 6px 0 0;
        }

        .ie7 &, .ie8 & {
            font-size: 14px;
        }
    }

    &_large &__link {
        font-size: 0.875rem;
        padding: 10px;

        .ie7 &, .ie8 & {
            font-size: 14px;
        }
    }
}

@keyframes dropdown {
      0% { transform: translateY( -20px ); opacity: 0; }
     92% { transform: translateY(   2px ); }
    100% { transform: translateY(   0   ); opacity: 1; }
}