.nav-btn {
    display: flex;
    justify-content: center;

    &__link {
        @width: 13px;
        @height: 24px;


        &-prev {
            .icon-single(@width, @height, "icons/path-prev");
            .link-position();
        }

        &-prev-inactive {
            .icon-single(@width, @height, "icons/path-prev-inactive");
            .link-position();
        }

        &-next {
            .icon-single(@width, @height, "icons/path-next");
            .link-position();
        }

        &-next-inactive {
            .icon-single(@width, @height, "icons/path-next-inactive");
            .link-position();
        }
    }
}

.link-position() {
    position: absolute;
    top: ~"calc((100% - 312px)/2)";
    margin-top: 206px;
    //.margin(206px, 0, 0, -7px);

    .ie7 &, .ie8 &, .ie9 & {
        margin-left: 25px;

        @media @bpLessLarge {
            margin-left: 12px;
        }
    }

    .rtl(transform, rotate(180deg));
}