.container {
    margin-left: auto;
    margin-right: auto;
    padding-right: 20px;
    padding-left: 20px;
    max-width: 100%;
}

.container-fluid {
    padding-right: 10px;
    padding-left: 10px;
}

@media only screen and (min-width: 1200px) {
    .container {
        width: 1500px;
    }
}

.row {
    box-sizing: border-box;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-flex: 0;
    -ms-flex: 0 1 auto;
    flex: 0 1 auto;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -ms-flex-direction: row;
    flex-direction: row;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    margin-right: -5px;
    margin-left: -5px;
}

.row.reverse {
    -webkit-box-orient: horizontal;
    -webkit-box-direction: reverse;
    -ms-flex-direction: row-reverse;
    flex-direction: row-reverse;
}

.col.reverse {
    -webkit-box-orient: vertical;
    -webkit-box-direction: reverse;
    -ms-flex-direction: column-reverse;
    flex-direction: column-reverse;
}

.generate(@colname) {
    .col-@{colname},
    .col-@{colname}-1,
    .col-@{colname}-2,
    .col-@{colname}-3,
    .col-@{colname}-4,
    .col-@{colname}-5,
    .col-@{colname}-6,
    .col-@{colname}-7,
    .col-@{colname}-8,
    .col-@{colname}-9,
    .col-@{colname}-10,
    .col-@{colname}-11,
    .col-@{colname}-12,
    .col-@{colname}-13,
    .col-@{colname}-14,
    .col-@{colname}-15,
    .col-@{colname}-16,
    .col-@{colname}-17,
    .col-@{colname}-18,
    .col-@{colname}-19,
    .col-@{colname}-20,
    .col-@{colname}-offset-0,
    .col-@{colname}-offset-1,
    .col-@{colname}-offset-2,
    .col-@{colname}-offset-3,
    .col-@{colname}-offset-4,
    .col-@{colname}-offset-5,
    .col-@{colname}-offset-6,
    .col-@{colname}-offset-7,
    .col-@{colname}-offset-8,
    .col-@{colname}-offset-9,
    .col-@{colname}-offset-10,
    .col-@{colname}-offset-12,
    .col-@{colname}-offset-13,
    .col-@{colname}-offset-14,
    .col-@{colname}-offset-15,
    .col-@{colname}-offset-16,
    .col-@{colname}-offset-17,
    .col-@{colname}-offset-18,
    .col-@{colname}-offset-19,
    .col-@{colname}-offset-20 {
        box-sizing: border-box;
        -webkit-box-flex: 0;
        -ms-flex: 0 0 auto;
        flex: 0 0 auto;
        padding-right: 5px;
        padding-left: 5px;
    }

    .col-@{colname} {
        -webkit-box-flex: 1;
        -ms-flex-positive: 1;
        flex-grow: 1;
        -ms-flex-preferred-size: 0;
        flex-basis: 0;
        max-width: 100%;
    }

    .generate-cols(@colname);

    .start-@{colname} {
        -webkit-box-pack: start;
        -ms-flex-pack: start;
        justify-content: flex-start;
        text-align: start;
    }

    .center-@{colname} {
        -webkit-box-pack: center;
        -ms-flex-pack: center;
        justify-content: center;
        text-align: center;
    }

    .end-@{colname} {
        -webkit-box-pack: end;
        -ms-flex-pack: end;
        justify-content: flex-end;
        text-align: end;
    }

    .top-@{colname} {
        -webkit-box-align: start;
        -ms-flex-align: start;
        align-items: flex-start;
    }

    .middle-@{colname} {
        -webkit-box-align: center;
        -ms-flex-align: center;
        align-items: center;
    }

    .bottom-@{colname} {
        -webkit-box-align: end;
        -ms-flex-align: end;
        align-items: flex-end;
    }

    .around-@{colname} {
        -ms-flex-pack: distribute;
        justify-content: space-around;
    }

    .between-@{colname} {
        -webkit-box-pack: justify;
        -ms-flex-pack: justify;
        justify-content: space-between;
    }

    .first-md {
        -webkit-box-ordinal-group: 0;
        -ms-flex-order: -1;
        order: -1;
    }

    .last-@{colname} {
        -webkit-box-ordinal-group: 2;
        -ms-flex-order: 1;
        order: 1;
    }

    .initial-order-@{colname} {
        -webkit-box-ordinal-group: NaN;
        -ms-flex-order: initial;
        order: initial;
    }
}

.generate-cols(@colname, @i: 1) when (@i <= 20) {
    @size: @i * 5%;

    .col-@{colname}-@{i} {
        -ms-flex-preferred-size: @size;
        flex-basis: @size;
        max-width: @size;
    }

    .col-@{colname}-offset-@{i} {
        margin-left: @size;
    }

    .generate-cols(@colname, @i + 1);
}

.generate(xs);

@media only screen and (min-width: 576px) {
    .generate(sm);
}

@media only screen and (min-width: @mediaBpMediumMin) {
    .generate(md);
}

@media only screen and (min-width: 992px) {
    .generate(lg);
}

@media only screen and (min-width: 1200px) {
    .generate(xl);
}

@media only screen and (max-width: 575px) {
    .hidden-xs {
        display: none;
    }
}

@media only screen and (min-width: 576px) and (max-width: @mediaBpSmallMax) {
    .hidden-sm {
        display: none;
    }
}

@media only screen and (min-width: @mediaBpMediumMin) and (max-width: 991px) {
    .hidden-md {
        display: none;
    }
}

@media only screen and (min-width: 992px) and (max-width: 1199px) {
    .hidden-lg {
        display: none;
    }
}

@media only screen and (min-width: 1200px) {
    .hidden-xl {
        display: none;
    }
}