@import (reference) "../styles";

.search-results {
    background: @bgColorNormal;
    font-size: @fontSize;

    &__content {
        padding: @generalGridSpacing 0;
    }

    &__list {
        list-style: none;
        margin: 0;
        padding: 0;
    }

    &__total {
        font-size: smaller;
        color: @textColorSecondary;
        margin: 0 0 @grid*2;
    }
}

.search-result {
    margin-bottom: 2em;

    &__title {
        text-decoration: none;
    }

    &__content {
        font-size: smaller;
        margin: .75em 0;
    }
}