@import (reference) "../styles";

.content {
    height: 100%;
    font-size: 1rem;
    line-height: 1.4;

    overflow-y: scroll;
    overflow-x: hidden;
    .scrollbar(5px);



    @media @bpMedium {
        /*margin-bottom: 95px;*/
    }
    
    @media @bpLessMedium {
        padding-top: 70px;
        overflow-y: auto;

    }

    > .container,
    > .container > .row {
        height: 100%;
        
        .ie7 &, .ie8 &, .ie9 &, .ie10 & {
            height: auto;
        }
    }

    &.is-loading {
        background: transparent url("loader.gif") no-repeat;
        background-position: 50% 33%;
        background-size: 50px;
        transition: background-size @animationDuration 0s @animationFunction;

        > .container {
            transition: opacity @animationDuration 0s @animationFunction;
            opacity: .5;
        }
    }

    .ie7 &, .ie8 & {
        font-size: 14px;
    }

    &.swipe-left {
        transition: transform 300ms linear;
        transform: translate(-100%, 0);
    }

    &.swipe-center {
        transform: translate(0, 0);
    }

    &.swipe-right {
        transition: transform 300ms linear;
        transform: translate(100%, 0);
    }
}

.cont {
    padding: 0 0 40px;
}

.all-in-one-toc {
    display: none;
}