@import (reference) "../styles";

.aside {
    padding: 0 0 39px;
    background: @bgColorNormal;

    @media @bpMedium {
        position: absolute;
        top: 205px;
        width: 312px;
        bottom: 0;

        &__inner {
            height: 100%;
        }

        &__menu {
            height: 100%;
            .scroll();
        }
    }

    @media @hideHeader {
        top: 116px;
    }

    .ie7 & {
        zoom: 1;
    }

    &__inner {
        .ie7 & {
            zoom: 1;
        }
    }
}