.footer {
    background: @footerBg;
    color: @footerText;
    font-size: .8rem;
    font-weight: 300;
    padding: 0 0 40px;

    @media @bpLessMedium {
        padding: 0 0 20px;
        font-size: .8rem;

        &_inner {
            padding: 0 20px 20px;
            margin-left: -20px;
            width: 100%;
            box-sizing: content-box;
        }
    }

    .ie7 &, .ie8 & {
        font-size: 12px;
    }

    &__divider {
        border-top: 1px solid #e4e4e4;
        padding: 0 0 20px;
    }
    
    &__logo {
        .text-align(right);
        padding: 10px 0;
    }

    &__text {
        flex: 1;
        font-weight: 300;
        overflow: hidden;
        text-align: center;

        @media @bpMedium {
            .text-align(left);
        }
    }

    &__links {
        margin: 15px 0 0;
        text-align: center;

        @media @bpMedium {
            margin: 0;
            .text-align(right);
        }
    }

    &__links-item {
        .margin(0, 0, 0, 20px);
        color: @footerText;
        
        @media @bpLessMedium {
            margin: 0 10px;
        }
        
    }

    &__link {
        display: inline-block;
        line-height: 1.2;
        //color: #fff;
        //.backunderline();
    }

    &_dark {
        background: @footerDarkBg;
        color: @footerDarkText;
        padding: 40px 0;
    }

    &_dark &__link {
        color: #fff;
    }

    &_dark &__links-item {
        .margin(0, 10px, 0, -10px);
        padding: 0 10px;

        border: solid darken(@footerDarkText, 25%);
        border-color: fade(@footerDarkText, 50%);
        .border-width( 0, 0, 0, 1px );

        &:first-child {
            border-color: transparent;
        }
    }

    @media @bpMedium {
        &_fixed {
            position: absolute;
            left: 0;
            right: 0;
            bottom: 0;
            width: 100%;
        }
    }
}
