@import (reference) "../styles";

@iconSmallWidth: 56px;
@iconSmallHeight: 64px;

.top-bar {
    padding: 20px 0 30px;

    &__product-box {
        display: flex;
        align-items: center;

        &:after {
            .ie7 &, .ie8 &, .ie9 & {
                content: '';
                clear: both;
            }
        }
    }

    &__product {
        @spriteNameSm: "products-64_34.png";
        @spriteSizeSm: image-width("../../images/@{spriteNameSm}");

        flex-shrink: 0;
        display: inline-block;

        width: (@iconSmallWidth);
        height: (@iconSmallHeight);
        background: url(product_logo.png) no-repeat center;

        .background-position-loop (@list, @i: 1, @posSm) when (@i <= length(@list)) {
            @type: extract(@list, @i);

            &_@{type} {
                background-image: url("/static/@{spriteNameSm}");
                background-size: @spriteSizeSm;
                background-position: unit(-30 - 76*(@i - 1), px) -@posSm;
            }

            .background-position-loop(@list, @i + 1, @posSm);
        }

        .background-position-loop (@list: @products_b2c, @posSm:30px);
        .background-position-loop (@list: @products_b2b, @posSm:148px);

        .ie7 &, .ie8 &, .ie9 & {
            float: left;
            .rtl-root(float, right);
        }
    }

    &__title {
        margin: 0 20px;
        flex: 0 1 auto; /* ie10 fix */

        .ie7 &, .ie8 &, .ie9 & {
            max-width: 50%;
            float: left;
            .rtl-root(float, right);
            margin-top: 12px;
        }
    }

    &__title-small {
        font-size: 0.75rem;
        display: block;

        .ie7 &, .ie8 &, .ie9 & {
            font-size: 12px;
        }
    }

    &__links-box {
        display: flex;
        justify-content: space-between;
        height: 100%;
    }

    &__langs {
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: flex-end;

        .dropdown {
            line-height: 1.25;
        }

        .ie7 &, .ie8 &, .ie9 & {
            float: right;
            .rtl-root(float, left);
            margin-top: 12px;
        }
    }

    &__versions {
        .margin(0, 20px, 0, 0);

        .ie7 &, .ie8 &, .ie9 &  {
            margin-top: 12px;
        }
    }
    
    &__links {
        line-height: 1.5;
        height: 100%;
        display: flex;
        align-items: center;

        .ie7 &, .ie8 &, .ie9 & {
            float: left;
            .rtl-root(float, right);
            margin-top: 12px;
        }
    }

    &__link {
        .margin(0, 20px, 0, 0);
        font-size: 0.85rem;
        .backunderline();
        white-space: nowrap;

        .ie7 &, .ie8 &, .ie9 & {
            font-size: 13px;
        }

        .is-offline &_feedback {
            display: none;
        }
    }

    &__icon {
        .margin (-4px, 5px, 0, 0);
        vertical-align: middle;

        .ie7 & {
            .margin (-4px, @grid*2, -5px, 0);
        }

        @width: 16px;
        @height: 16px;

        &_feedback {
            .icon-single(@width, @height, "icons/email-gray");
        }

        &_print {
            .icon-single(@width, @height, "icons/print-gray");
        }

        &_support {
            .icon-single(@width, @height, "icons/support-gray");
        }
        
        &_save-as-pdf {
            .icon-single(@width, @height, "icons/save-as-pdf");
        }
    }

    &__triangle {
        border: 3px solid transparent;
        border-top-color: @textColorLink;
        display: inline-block;
        vertical-align: middle;
        .margin(0, 0, 0, 3px);
    }
}
