.menu-box {
    .scroll();

    @media @bpLessMedium {
        background: @bgColorNormal;
        visibility: hidden;
        position: fixed;
        top: @mobileHeaderHeight;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 3;
        .padding(0, 0, 0, 20px);

        transition: @transition;
        transition-property: transform, visibility;
        transition-delay: 0s;
        pointer-events: none;
        transform: translateX(-100%);
        .rtl( transform, translateX(100%) );

        &.is-active {
            visibility: visible;
            pointer-events: initial;
            transform: translateX(0);
            .rtl(transform, translateX(0));
            transition-delay: 0s, 0s, @animationDuration;
        }
    }
}