@import (reference) "../styles";

.pic,
.b-pic {
    display: inline-block;
    position: relative;
    margin: 0;
    padding: 0;
    max-width: 100%;
    font-size: 0;
    vertical-align: text-bottom;
    background-size: 100%;
    background-repeat: no-repeat;

    &__img {
        visibility: hidden;
    }
}
