.selector-mobile {
    padding: 15px 20px;
    .margin(0, 0, 0, -20px);

    @media @bpMedium {
        display: none;
    }

    &_main {
        margin: 0 -20px 20px;
        @media @bpSmall {
            display: none;
        }
    }

    & + & {
        padding-top: 0;
    }
}