@import (reference) "../styles";

@media print {

    .aside,
    .mobile-menu,
    .mobile-search-results,
    .selector-mobile,
    .header,
    .top-bar__product,
    .top-bar__versions,
    .top-bar__links,
    .top-bar__langs,
    .nav-btn,
    .content > .links,
    .content .bt-home,
    .page-title__btn,
    .aside,
    .relatedtopics,
    .belowtopictext,
    .feedback,
    .cookie-policy,
    .footer {
        display: none !important;
    }

    html {
        overflow: visible !important;
    }

    body {
        overflow: visible !important;
        height: auto !important;
    }
    
    .content {
        max-height: none !important;
        height: auto !important;
        overflow-y: visible !important;
        -webkit-print-color-adjust: exact;
        print-color-adjust: exact;
    }

    .page-title {
        min-height: 0 !important;

        &__text {
            padding: 0 0 @grid !important;
            .text-align( left );
        }
    }
    
    .top-bar__title {
        margin: 0 !important;
    }

    .main,
    .row,
    .container {
        display: block !important;
        height: auto !important;
        overflow: auto !important;
    }

    .row > div {
        display: block !important;
        padding: 0 !important;
    }

    .content .heading1 {
        margin-top: .5em;
    }

    .cont {
        padding: 0 !important;
    }

    .main {
        display: block;
        overflow: auto !important;
        overflow-x: auto !important;
    }

    .cont > .heading1:first-child,
    .cont > .heading2:first-child,
    .cont > .heading3:first-child,
    .cont > .heading4:first-child,
    .cont > .heading5:first-child,
    .cont > .heading6:first-child{
        margin-top: 0;
    }
    
    .all-in-one-toc {
        display: block !important;
        margin: 0 0 40px;

        a {
            page-break-inside: avoid;
        }
    }

    .cont {
        h1, h2, h3, h4, h5, h6, p {
            page-break-inside: avoid;
        }
    }
}