@import (reference) "../styles";

.mobile-search-results {
    @boxShadowActive:  0 0 10px  fade( black, 25% );
    @boxShadowInactive: 0 0 10px fade( black, 0 );
    display: none;

    position: absolute;
    top: @mobileHeaderHeight;
    left: 0;
    right: 0;
    bottom: 0;

    background: @bgColorNormal;
    margin: 0;
    padding: 0 @mobilePaddings 0;
    overflow: auto;
    overflow-x: hidden;
    visibility: visible;

    transition: @transition;
    transition-property: transform, box-shadow, visibility;
    transition-delay: 0s;
    pointer-events: initial;

    @media @bpLessMedium {
        display: block;
    }

    &.is-hidden {
        display: none;
        pointer-events: none;
        visibility: hidden;
        transform: translateX(100%);
        box-shadow: @boxShadowInactive;
        transition-delay: 0s, 0s, @animationDuration;

        .rtl( transform, translateX(-100%) );
    }

    &.is-active {
        z-index: 1;
    }

    &__list {
        margin: 0;
        padding: 0;
        list-style: none;

    }

    &__total {
        font-size: 0.84rem;
        color: @textColorNormal;
        padding: @grid*2 20px;
        background: @markColor;
        width: 100%;
        box-sizing: content-box;
        margin: 0 0 20px -20px;
        font-weight: 600;
    }
}

.mobile-search-result {
    margin: 0 0 20px;
    padding: 0;
    list-style: none;

    &__title {
        text-decoration: none;
        font-size: 1rem;
        margin: 0 0 10px;
    }

    &__content {
        font-size: 0.875rem;
    }
}