@import (reference) "../styles";

.scrollbar( @width: 10px, @padding: 1px ) {
    @bgColor: @bgColorAccented;
    @thumbColor: @borderColor;

    :root:not(.is-hv) &::-webkit-scrollbar {
        width: @width + @padding*2;

        &-track {
            background: fade( @bgColor, 100% );
            border: @padding solid transparent;
            background-clip: content-box;
        }

        &-thumb {
            background: fade( @thumbColor, 66% );
            border: @padding solid transparent;
            background-clip: content-box;
        }
    }

    :root:not(.is-hv) &:hover::-webkit-scrollbar{

        &-thumb {
            background-color: @thumbColor;
        }
    }
}