.button {
    background: @bgColorNormal;
    font-size: 0.875rem;
    color: @textColorLink;
    min-width: 80px;
    text-align: center;
    line-height: 1;
    padding: 9px 19px;
    border: 1px solid rgba(123, 129, 134, 0.6);
    cursor: pointer;
    
    & + & {
        .margin(0, 0, 0, 10px);
    }
    
    &:hover {
        color: @textColorNormal;
    }

    .ie7 &, .ie8 & {
        font-size: 14px;
        border: 1px solid rgb(176, 179, 182);
    }
    
    &_back {
        position: relative;
        .padding(10px, 19px, 8px, 45px);
        
        &:before {
            content: '';            
            position: absolute;
            left: 19px;
            top: 9px;
        }
    }
}