@import (reference) "../styles";

.tooltip {
    @arrowSize: @grid*4;
    @blockSize: @grid*125;
    @arrowOffset: 60px;

    display: none;
    position: absolute;
    z-index: 2;

    max-width: 550px;

    padding: @grid*2 @grid*2;

    background: @bgNotes;
    box-shadow: 0 0 3px hsla( 0, 0%, 0%, .33 );

    border: none;
    background: @bgNotes;

    font-weight: 300;
    font-size: 0.875rem;
    cursor: default;

    &__content {
        height: 100%;
        padding: inherit;
        overflow: auto;

        & > a > img {
            position: relative;
            z-index: 3;

            .rtl(float, left);
        }
    }

    ul, ol {
        font-size: .85em;
        margin: .5em 0;
    }

    ul + p,
    ol + p {
        margin-top: 1.4em;
    }

    :first-child {
        margin-top: 0;
    }

    :last-child {
        margin-bottom: 0;
    }

    .ie7 & {
        //margin: @grid*2 0 @grid*2;
        border: 1px solid hsl( 0,0%,85% );
    }

    .ie7 &, .ie8 & {
        font-size: 14px;
    }

    &:after {
        background: @bgNotes;
        content: '';
        position: absolute;
        transform: rotate( 45deg );
        top: 0;
        left: @arrowOffset;
        width: @arrowSize;
        height: @arrowSize;
        margin: -@arrowSize/2 -@arrowSize/2;
        z-index: -2;
        box-shadow: -1px -1px 2px 0 hsla(0, 0%, 0%, .2);
    }

    &.is-to-top {
        &:before {
            top: 100%;
            bottom: auto;
        }
        &:after {
            top: 100%;
            transform: rotate(-135deg);
        }
    }

    &.is-to-left:after {
        left: auto;
        right: @arrowOffset;
    }
}