.cookie-policy {
    position: fixed;
    display: flex;
    /*flex-direction: row-reverse;*/
    align-items: center;
    bottom: 0;
    width: 100%;
    background-color: rgba(34, 34, 34, 0.8);
    color: #eee;
    padding: 10px 20px;
    z-index: 9999;
    font-size: 0.8125rem;
    text-align: center;

    @media @bpLessMedium {
        display: block;
    }
    
    &.hidden {
        display: none;
    }
    
    .ie8 &, .ie9 & {
        display: block;
    }

    .ie8 & {
        background-color: rgb(34, 34, 34);
        font-size: 13px;
    }

    &__inner {
        flex: 1 1 auto;

        @media @bpLessMedium {
            padding: 0 0 10px;
        }

        @media @bpMedium {
            .ie8 &, .ie9 & {
                display: table-cell;
                width: 100%;
                vertical-align: middle;
            }
        }
    }

    &__btn-wrapper {
        flex: 0 1 auto;

        @media @bpMedium {
            .ie8 &, .ie9 & {
                display: table-cell;
                vertical-align: middle;
            }
        }
    }
    
    &__btn {
        padding: 8px 18px 7px 18px;
        background: #006D5C;
        border: none;
        color: #eee;
        cursor: pointer;
        font-size: 0.75rem;
        text-align: center;
        text-transform: uppercase;
        letter-spacing: -.2px;
        font-family: @fontFamily;
        margin-left: 10px;
        white-space: nowrap;

        .ie8 & {
            font-size: 12px;
        }

        &:hover {
            color: #fff;
        }
    }
    
    a {
        color: #18CCB0;
    }
}