/*
 *  Common
 *
 */
*,
*:before,
*:after {
  box-sizing: inherit;
}
@media screen and (-webkit-min-device-pixel-ratio: 0) {
  *,
  *:before,
  *:after {
    max-height: 999999px;
  }
}
.ie7 *,
.ie7 *:before,
.ie7 *:after {
  box-sizing: content-box;
  content: none;
}
html {
  margin: 0;
  padding: 0;
  font: 300 16px/1.25 KasperskySans, Helvetica, Arial, sans-serif;
  box-sizing: border-box;
  color: #333333;
  height: 100%;
  width: 100%;
  background-color: #fff;
  overflow: hidden;
}
@media only screen and (max-width: 849px) {
  html {
    color: #5c5f61;
  }
  html.is-opera-mini {
    height: auto;
    overflow: auto;
  }
}
html[lang="el-GR"],
html[lang="vi-VN"] {
  font-family: NotoSans, sans-serif;
}
body#who-content {
  height: 100%;
  margin: 0 auto;
  padding: 0;
  font: inherit;
}
.ie7 body#who-content {
  font: 300 16px/1.25 KasperskySans, Helvetica, Arial, sans-serif;
}
[lang="el-GR"] .ie7 body#who-content,
[lang="vi-VN"] .ie7 body#who-content {
  font-family: NotoSans, sans-serif;
}
@media only screen and (max-width: 849px) {
  .is-opera-mini body#who-content {
    height: auto;
  }
}
a {
  color: #006d5c;
  text-decoration: underline;
}
a:hover {
  text-decoration: none;
}
h1 {
  font-size: 1rem;
  font-weight: 700;
}
.ie7 h1,
.ie8 h1 {
  font-size: 16px;
}
input[type="text"],
input[type="password"],
input[type="search"],
input[type="email"] {
  border: 1px solid #cccccc;
  border-radius: 0;
  padding: 8px 12px;
  appearance: none;
}
input[type="text"]:focus,
input[type="password"]:focus,
input[type="search"]:focus,
input[type="email"]:focus {
  outline: none;
}
input[type="search"]::-webkit-search-cancel-button {
  display: none;
}
a,
button {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
a:active,
button:active,
a:focus,
button:focus {
  outline: none;
}
@media only screen and (max-width: 849px) {
  .is-responsive a:active,
  .is-responsive button:active {
    opacity: .8;
  }
}
a,
button,
input {
  pointer-events: initial;
}
img {
  border: none;
  vertical-align: text-bottom;
}
mark {
  background: #f8e81c;
  color: #5c5f61;
  padding: 0 2px;
  margin: 0 -2px;
  border-radius: 3px;
}
a mark {
  color: #006d5c;
}
.g-hidden {
  display: none !important;
}
@media only screen and (max-width: 849px) {
  .is-responsive .desktop-only {
    display: none;
  }
}
.mobile-only {
  display: none;
}
@media only screen and (max-width: 849px) {
  .is-responsive .mobile-only {
    display: block;
  }
}
.g-scrollable {
  -webkit-overflow-scrolling: touch;
  -moz-overflow-scrolling: touch;
  overflow-scrolling: touch;
}
