@media print {
  .aside,
  .mobile-menu,
  .mobile-search-results,
  .selector-mobile,
  .header,
  .top-bar__product,
  .top-bar__versions,
  .top-bar__links,
  .top-bar__langs,
  .nav-btn,
  .content > .links,
  .content .bt-home,
  .page-title__btn,
  .aside,
  .relatedtopics,
  .belowtopictext,
  .feedback,
  .cookie-policy,
  .footer {
    display: none !important;
  }
  html {
    overflow: visible !important;
  }
  body {
    overflow: visible !important;
    height: auto !important;
  }
  .content {
    max-height: none !important;
    height: auto !important;
    overflow-y: visible !important;
    -webkit-print-color-adjust: exact;
    print-color-adjust: exact;
  }
  .page-title {
    min-height: 0 !important;
  }
  .page-title__text {
    padding: 0 0 4px !important;
    text-align: left;
  }
  [dir="rtl"] .page-title__text {
    text-align: right;
  }
  .top-bar__title {
    margin: 0 !important;
  }
  .main,
  .row,
  .container {
    display: block !important;
    height: auto !important;
    overflow: auto !important;
  }
  .row > div {
    display: block !important;
    padding: 0 !important;
  }
  .content .heading1 {
    margin-top: .5em;
  }
  .cont {
    padding: 0 !important;
  }
  .main {
    display: block;
    overflow: auto !important;
    overflow-x: auto !important;
  }
  .cont > .heading1:first-child,
  .cont > .heading2:first-child,
  .cont > .heading3:first-child,
  .cont > .heading4:first-child,
  .cont > .heading5:first-child,
  .cont > .heading6:first-child {
    margin-top: 0;
  }
  .all-in-one-toc {
    display: block !important;
    margin: 0 0 40px;
  }
  .all-in-one-toc a {
    page-break-inside: avoid;
  }
  .cont h1,
  .cont h2,
  .cont h3,
  .cont h4,
  .cont h5,
  .cont h6,
  .cont p {
    page-break-inside: avoid;
  }
}
