.tooltip {
  display: none;
  position: absolute;
  z-index: 2;
  max-width: 550px;
  padding: 8px 8px;
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.33);
  border: none;
  background: #f9f9f3;
  font-weight: 300;
  font-size: 0.875rem;
  cursor: default;
}
.tooltip__content {
  height: 100%;
  padding: inherit;
  overflow: auto;
}
.tooltip__content > a > img {
  position: relative;
  z-index: 3;
}
[dir="rtl"] .tooltip__content > a > img {
  float: left;
}
.tooltip ul,
.tooltip ol {
  font-size: .85em;
  margin: .5em 0;
}
.tooltip ul + p,
.tooltip ol + p {
  margin-top: 1.4em;
}
.tooltip :first-child {
  margin-top: 0;
}
.tooltip :last-child {
  margin-bottom: 0;
}
