  border: 1px solid #d9d9d9;
}
.ie7 .tooltip,
.ie8 .tooltip {
  font-size: 14px;
}
.tooltip:after {
  background: #f9f9f3;
  content: '';
  position: absolute;
  transform: rotate(45deg);
  top: 0;
  left: 60px;
  width: 16px;
  height: 16px;
  margin: -8px -8px;
  z-index: -2;
  box-shadow: -1px -1px 2px 0 rgba(0, 0, 0, 0.2);
}
.tooltip.is-to-top:before {
  top: 100%;
  bottom: auto;
}
.tooltip.is-to-top:after {
  top: 100%;
  transform: rotate(-135deg);
}
.tooltip.is-to-left:after {
  left: auto;
  right: 60px;
}
